// Equifit module
define('modules/equifit/entities/equifit',[
        '../../../app'
    ],
    function () {
        'use strict';
       var model = Backbone.Model.extend({
            defaults: {
                createdAt: null,
                updatedAt: null,
                updatedBy: null,
                clubName: null,
                complete: false
            }
        });

        return Backbone.Collection.extend({
            model: model,
            url: function () {
                return '/app/mocks/equifit/equifits.json';
            }
        });
    });

